@import "../variables/index";

.page-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  padding-left: 300px;
}

.content {
  margin: 0 auto;
  width: 1264px;
  flex: 1 0 auto;
  background-color: #fff;
  padding: 30px 30px;
  border-radius: 5px;
  border: 1px solid #e3e7e8;
}
